.my-account-div {
  width: 100%;
  padding-top: 2rem;
  padding-left: 8%;
  padding-right: 8%;
  padding-bottom: 5rem;
  background-color: rgb(250, 249, 249);
}
.my-account-heading {
  width: 100%;
  margin-bottom: 2rem;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.my-account-main {
  width: 100%;
  display: grid;
  grid-template-columns: 20% 80%;
}

.my-account-main-options {
  /* grid-column: 1; */
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  padding: 0.6rem;
}
.my-account-main-options button {
  width: 100%;
  padding: 0.5rem;
  /* margin-bottom: 0.5rem; */
  border-radius: 0;
  border: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: large;
  background-color: transparent;
}

.my-account-main-display {
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  /* height: 70vh; */
  background-color: white;
  padding-bottom: 0.5rem;
  /* grid-column: 2; */
}

.my-account-main-display-heading {
  margin-top: 1.5rem;
  /* display: grid;
  grid-template-columns: 96% 4%; */
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  /* border: 1px solid; */
  font-size: xx-large;
}

.my-account-main-display-addresses {
  width: 100%;
  display: grid;
  grid-template-columns: 98% 2%;

  border: 1px solid lightgray;
  padding: 1rem;
  margin-bottom: 0.3rem;
}
.my-account-main-display-addresses p {
  font-size: 18px;
  margin-bottom: 0.2rem;
  letter-spacing: 0.1rem;
  font-family: Arial, Helvetica, sans-serif;
}

.my-account-main-display-add-address-section-1 {
  width: 100%;
  display: grid;
  grid-template-columns: 48% 4% 48%;
  /* justify-content: space-between; */
}
.my-account-main-display-add-address-section-1-1 {
  width: 100%;
}
.my-account-main-display-add-address label {
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.2rem;
  font-size: 1.1rem;
  margin-bottom: rem;
}
.my-account-main-display-add-address-section-1-1 input {
  width: 100%;
  height: 2rem;
  font-weight: 100;
  font-size: 18px;
  padding: 1.1rem;
  margin-top: 0.5rem;
  padding-left: 0.3rem;
  margin-bottom: 1.3rem;
}

.my-account-main-display-add-address-section-2 input {
  width: 100%;
  height: 2rem;
  font-weight: 100;
  font-size: 18px;
  padding: 1.1rem;
  margin-top: 0.5rem;
  padding-left: 0.3rem;
  margin-bottom: 1.3rem;
}
.my-account-main-display-add-address-btn button {
  width: 20rem;
  height: 2.3rem;
  border: none;
  background-color: rgb(250, 129, 129);
  color: white;
  font-size: 16px;
  letter-spacing: 0.2rem;
}

.myaccount-myorders-status {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding-top: 2rem;
  border-top: 1px solid lightgray;
}
.myaccount-myorders-status p {
  font-size: 18px;
  color: rgb(100, 100, 100);
}

.myaccount-myorders-products {
  padding: 0.8rem;
  border-top: 1px solid lightgray;
  padding-top: 1rem;
  display: grid;
  grid-template-columns: 30% 55% 15%;
}
.myaccount-myorders-product-img {
  width: 9rem;
  height: 9rem;
}
.myaccount-myorders-product-detail {
  display: flex;
  padding: 0.4rem;
  flex-direction: column;
  justify-content: space-between;
}
.myaccount-myorders-product-status {
  padding: 0.4rem;
}
.myaccount-myorders-shipping {
  border: 1px solid lightgray;
  padding: 0.8rem;
}
.myaccount-myorders-shipping p {
  color: gray;
}

@media only screen and (max-width: 900px) {
  .my-account-main {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
  }
  .my-account-main-display-add-address label {
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.1rem;
    font-size: 0.9rem;
    /* margin-bottom: rem; */
  }
  .my-account-main-display-add-address-section-1-1 input {
    width: 100%;
    height: 1.8rem;
    font-weight: 100;
    font-size: 16px;
    padding: 1.1rem;
    margin-top: 0.5rem;
    padding-left: 0.3rem;
    margin-bottom: 1rem;
  }

  .my-account-main-display-add-address-section-2 input {
    width: 100%;
    height: 1.8rem;
    font-weight: 100;
    font-size: 16px;
    padding: 1.1rem;
    margin-top: 0.5rem;
    padding-left: 0.3rem;
    margin-bottom: 1rem;
  }
  .myaccount-myorders-status p {
    font-size: 18px;
  }
  .myaccount-myorders-product-detail p {
    font-size: 16px;
  }
  .myaccount-myorders-product-status p {
    font-size: 16px;
  }
  /* .myaccount-myorders-product-img {
    width: 6rem;
    height: 6rem;
  } */
}

@media only screen and (max-width: 700px) {
  .my-account-main-display {
    width: 100%;
    padding-left: 1%;
    padding-right: 1%;
    /* height: 70vh; */

    padding-bottom: 0.5rem;
    /* grid-column: 2; */
  }
  .myaccount-myorders-product-detail p {
    font-size: 14px;
  }
  .myaccount-myorders-status p {
    font-size: 16px;
  }

  .myaccount-myorders-product-img {
    width: 7rem;
    height: 7rem;
  }
}

@media only screen and (max-width: 560px) {
  .my-account-div {
    padding-top: 2rem;
    padding-left: 3%;
    padding-right: 3%;
    /* padding-bottom: 5rem;
    background-color: rgb(250, 249, 249); */
  }
  .myaccount-myorders-product-detail p {
    font-size: 14px;
  }
  .myaccount-myorders-product-status p {
    font-size: 14px;
  }
  .myaccount-myorders-status p {
    font-size: 14px;
  }

  .myaccount-myorders-product-img {
    width: 5rem;
    height: 5rem;
  }
}
